import React, { useState } from 'react';
import apiService from '../../api/api';
import buttonImg from '../../img/button.png';
import phone from '../../img/phone.png';
import { Device } from '@twilio/voice-sdk';
import './Button.css';

function Button() {
  const [isDecline, setIsDecline] = useState(false);
  const [deviceTwilio, setDeviceTwilio]: any = useState({});
  
  const handleCall = () => {
    setIsDecline(!isDecline);
    apiService.generateToken()
    .then(async (data: any) => {
        console.log("phone number", "+1 929 424 0059");
        let device = new Device(data.data.token);
        await device.connect(
          {params: {
          To: "+1 929 424 0059"
        }});
        setDeviceTwilio(device);
    })
  } 

  const declineCall = () => {
    console.log("decline");
    setIsDecline(!isDecline);
    deviceTwilio.disconnectAll();
  }  

  return (
    <>
      <div>
        <img className="img-button" src={buttonImg} onClick={() => isDecline ? declineCall() : handleCall()} />
        <button className="call-button" onClick={() => handleCall()}>
          <div className="flex-button">
            <img className="phone" src={phone}/>
            <p className="button-value">Lets Talk!</p>
          </div>
        </button>
      </div>
    </>
  );
}

export default Button;
